@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.main {
  display: flex;

  @include notebook {
    flex-direction: column-reverse;
  }
}

.content {
  flex-grow: 1;
}

.sidebar {
  margin-left: 150px;
  flex: 0 0 400px;

  @include notebook {
      margin-left: 0;
      flex: 0 0 auto;
  }

  &__block {
    padding: 15px;
    background-image: var(--info-sidebar-gradient);
    border-radius: 10px;

    @include notebook {
      margin-bottom: 15px;
    }

    h2 {
      margin: 0 0 10px;
      font-weight: 600;
    }

    * + h2 {
        margin-top: 20px;
    }

    &::v-deep p + p {
        margin-top: 10px;
    }

    &::v-deep p > a {
        margin-top: 0;
    }

    &::v-deep a {
      display: inline-block;
      margin-top: 10px;
      color: var(--text-tertiary);

      &:after {
        content: '';
        display: inline-block;
        width: 11px;
        height: 10px;
        margin-left: 3px;
        background: url('../assets/themes/#{$brand-name}/images/icon-chevron.svg');
      }
    }

    p > a {
      margin: 0;
      color: $primary;

      &:after {
        display: none;
      }
    }

    &::v-deep strong {
        font-weight: 700;
        word-break: break-word;
    }
  }

  @include notebook {
    max-width: 100%;
    width: 100%;
  }
}

.main::v-deep {
  * + h1 {
    margin-top: 40px;
  }

  h1,
  h3 {
    color: var(--text-tertiary);
    font-size: 30px;
    margin-bottom: 20px;
  }

  h2 {
    color: var(--text-primary);
    font-size: 20px;
    margin-bottom: 5px;
    margin-top: 15px;
    font-weight: 500;

    & + h2 {
      margin-top: 5px;
    }
  }

  p {
    line-height: 18px;
    margin-bottom: 5px;
    text-align: justify;
  }

  p + img {
    margin-top: 10px;
  }

  ol {
    list-style: inside decimal;
    margin-bottom: 5px;
  }

  ul {
      list-style: inside disc;
      margin-bottom: 5px;
  }

  li {
      line-height: 18px;
      margin-top: 5px;
  }

  strong {
      font-weight: 700;
  }

  em {
    font-style: italic;
  }
}
